@mixin start-style-large-des {  color: #eeeeee;  font-size: 24px;  font-family: 'Sora', sans-serif;  line-height: 30px;}@mixin start-style-tablets {  color: #eeeeee;  font-size: 20px;  font-family: 'Sora', sans-serif;  line-height: 26px;}@mixin start-style-phone {  color: #eeeeee;  font-size: 16px;  font-family: 'Sora', sans-serif;  line-height: 22px;}