@import './Mixin';.usersContainer {  display: flex;  justify-content: center;  flex-wrap: wrap;  width: 100%;  gap: 10px;  margin: 20px auto;}@media only screen and (min-width: 600px) {  .usersContainer {    gap: 20px;  }}@media only screen and (min-width: 992px) {  .usersContainer {    gap: 30px;    max-width: 1200px;  }  .postVisible {    max-width: 570px;    > div {      @include start-style-tablets;      width: calc(50% - 35px);      padding: 33px 33px;      box-sizing: border-box;      font-size: 18px;      > button {        @include start-style-tablets;        padding: 16px 39px;      }    }  }}